import React, { useEffect, useState } from 'react'
import { Avatar, Button, FormControl, Grid, InputLabel, MenuItem, Select, TextField } from '@mui/material'
import { makeStyles } from "@mui/styles";
import { AI_update_user_detail, AI_user_detail } from 'API/userAPI';
import { useInstance } from 'easy-redux-http-call';
import { useFormik } from 'formik';
import { generateFormikInput } from 'utils';
import * as yup from "yup";
import HeaderWrapper from 'components/Wrapper/HeaderWrapper';
import { LoadingAnimation } from 'containers/SuspenseFallback/SuspenseFallback';

const useStyles = makeStyles((theme) => ({
  dmUserProfileInfo: {
    padding: theme.spacing(1.5),
    borderRadius: theme.shape.borderRadius,
    display: "flex",
    position: "relative",
    marginTop: theme.spacing(1),
    width: "fit-content",
    margin: "auto",
    "&>div": {
      width: "100px",
      height: "100px",
    },
  },
  dmEditingProfile: {
    "&>div": {
      marginTop: theme.spacing(2),
      width: "100%"
    },
  },
  dmEditProfileIcon: {
    position: "absolute !important",
    right: 10,
    top: 10,
  },
  dmUserAction: {
    marginTop: theme.spacing(1),
    width: "fit-content",
    marginLeft: "auto",
  }
}))

const userProfileSchema = yup.object().shape({
  name: yup.string().required("Required"),
});

let init = {
  name: "",
  email: "",
}

export default function Account() {
  const classes = useStyles()
  const [initialState, setInitialState] = useState(init)

  const userState = useInstance(AI_user_detail)
  const updateUserState = useInstance(AI_update_user_detail)

  useEffect(() => {
    if (userState?.data?.user) {
      let user = userState?.data?.user
      setInitialState({
        name: user.name,
        email: user.email,
      })
    }
  }, [userState])


  const handleUpdate = (value) => {
    AI_update_user_detail.call({
      params: value
    })
  }

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: initialState,
    onSubmit: handleUpdate,
    validationSchema: userProfileSchema,
  });

  return (
    <HeaderWrapper backAction title="Account">
      {userState?.loading ? <LoadingAnimation /> :
        <Grid>
          <Grid className={classes.dmUserProfileInfo}>
            <Avatar src={userState?.data?.user?.profileUrl} alt="profile-img" >
              {userState?.data?.user?.name?.[0]?.toUpperCase() || "-"}
            </Avatar>
          </Grid>
          <Grid>
            <Grid className={classes.dmEditingProfile}>
              <TextField
                {...generateFormikInput(formik, "name")}
                fullWidth
                label="Name"
                placeholder="Name"
              />
              <TextField
                {...generateFormikInput(formik, "email")}
                disabled
                fullWidth
                label="Email"
                placeholder="Email"
              />


              <FormControl fullWidth>
                <InputLabel id="timezone-input">Timezone</InputLabel>
                <Select
                  disabled
                  defaultValue={userState?.data?.user?.timezone}
                  labelId="timezone-input"
                  label="Timezone"
                  value={userState?.data?.user?.timezone}
                >
                  <MenuItem value="Asia/Kolkata">Asia/Kolkata</MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid className={classes.dmUserAction}>
              <Button
                onClick={formik.handleSubmit}
                type="submit"
                disabled={updateUserState.loading}
              >
                Save
              </Button>
            </Grid>
          </Grid>
        </Grid>
      }

    </HeaderWrapper >
  )
}
